.education {
  background: white;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 1rem;
  font-size: 2rem;
  font-family: 'Poppins', sans-serif;
}

.education-header h1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 4rem;
  margin-bottom: 2rem;
}

.education-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
}

.education-container h1, p {
  font-weight: 200;
}

.education-container {
  margin: 0 auto 6rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  width: 100%;
  gap: 4rem;
}

.education-card {
  width: 90%;
  padding: 2rem;
  height: auto;
  border-radius: 0.4rem;
  box-shadow: 0 0 1rem 1rem rgb(233, 226, 244);
}

.card-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-name {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 0.4rem;
}

.card-heading h1 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.2rem;
  font-weight: 400;
}

.card-time {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 1.2rem;
}

.card-courses li {
  font-family: 'Poppins', sans-serif;
  font-weight: 200;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.card-uni {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-uni h1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  font-size: 1.2rem;
}